import { Button, Card, CardActions, CardContent, Typography } from '@mui/material'
import React from 'react'

const FeaturedPost = () => {
  return (
    <Card>
    <CardContent> 
        <Typography variant="h2" fontStyle="Arial" gutterBottom>Title of a longer feature blog post</Typography>
        <Typography variant="h5" fontStyle="Montserrat">Mutiple lines of text that form the lede, 
         new readers quickly and efficiently about what's more interesting in this post's content</Typography>
    </CardContent>
    <CardActions>
        <Button>
            Read more
        </Button>
    </CardActions>
    </Card>
  )
}

export default FeaturedPost
