import { Container, Grid, ThemeProvider, createTheme } from '@mui/material';
import './App.css';
import Header from './components/Header';
import FeaturedPost from './components/FeaturedPost';
import PostCard from './components/PostCard';
import { featuredPosts, sidebar } from './data/Data';
import { ViewSidebar } from '@mui/icons-material';
import Sidebar from './components/Sidebar';
import Main from './components/Main';
function App() {

  const darkTheme = createTheme({
    palette: {
      mode: 'dark',
    },
  });

  return( 
<ThemeProvider theme={darkTheme}>
  <Container>
    <Header />
    <FeaturedPost />
    <br/>
    <Grid container spacing={4}>
      {
      featuredPosts.map((post) => (
      <PostCard post={post} key={post.title}/>
      ))}
      
    </Grid>
    <br/>
    <br/>
    <br />
    <Grid container spacing={5}>
      <Main title='From the firehouse'/>
      <Sidebar title={sidebar.title} description={sidebar.description} archives={sidebar.archives} social={sidebar.social}></Sidebar>

    </Grid>
  </Container>
  
  </ThemeProvider>

  );
}

export default App;
